import { CardFilm } from "../components/CardFilm";
import {films} from "../base"

const Home = () => {
  return (
    <>
      <div className="list-film">
        {films.map((item) => (
          <CardFilm film={item} key={item.id} />
        ))}
      </div>
    </>
  );
};

export default Home;
