import { Link } from "react-router-dom";

export const CardFilm = ({ film }) => {
  return (
    <>
      <div className="card-film">
        <div className="card-film_img">
        <Link to={`/film/${film.id}`}><img src={film.baner} alt={film.title}/></Link>
        </div>
        <div className="card-film_title">
          <Link to={`/film/${film.id}`}>
            <span className="title">{film.title}</span>
          </Link>
        </div>
        <div className="card-film_action">
          <Link to={`/film/${film.id}`}><span className="buy">Купити білети</span></Link>
        </div>
      </div>
    </>
  );
};
