import { useState, useCallback, useEffect } from "react";

const Cart = () => {
  const [cart, setCart] = useState([]);
  const getCart = useCallback(() => {
    setCart(JSON.parse(localStorage.getItem("tickets") || "[]"));
  }, []);

  const toConfirm = useCallback(() => {
    localStorage.removeItem("tickets");
    setCart([]);
  }, []);

  const toCancel = useCallback(
    (event) => {
      const newCart = cart.filter(
        (item) => item.id !== event.target.dataset.id
      );
      localStorage.setItem("tickets", JSON.stringify(newCart));
      setCart(newCart);
    },
    [cart]
  );

  useEffect(() => {
    getCart();
  }, [getCart]);
  return (
    <>
      {cart.length === 0 ? (
        <div className="row">
          <div className="center"><h2 className='cartTitle'>Корзина порожня</h2></div>
        </div>
      ) : (
        <div className="row">
          <h3 className="center">Вибрані білети</h3>
          <table>
            <thead>
              <tr>
                <th>Назва фільму</th>
                <th>Ряд</th>
                <th>Місце</th>
                <th></th>
              </tr>
            </thead>

            <tbody>
              {cart.map((item, index) => (
                <tr key={index}>
                  <td>{item.title}</td>
                  <td>{item.row}</td>
                  <td>{item.place}</td>
                  <td>
                    <button
                      className="btn grey lighten-2 black-text"
                      onClick={toCancel}
                      data-id={item.id}
                    >
                      Скасувати
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          <div className="btn-success center">
            <button className="btn green lighten-1 center" onClick={toConfirm}>
              Підтвердити
            </button>
          </div>
        </div>
      )}
    </>
  );
};

export default Cart;
