import React, { useState, useEffect, useCallback } from "react";
import parse from "html-react-parser";


export const Places = ({film}) => {
  const [places, setPlaces] = useState("");

  const getTicketsFromLocalStorage = useCallback(() => {
    return JSON.parse(localStorage.getItem("tickets") || "[]");
  }, [])

  const addToLocalStorage = useCallback((ticket) => {
    const all = getTicketsFromLocalStorage();
    all.push(ticket);
    localStorage.setItem("tickets", JSON.stringify(all));
  }, [getTicketsFromLocalStorage])
  
  console.log(film)

  const buyTicket = useCallback((event) => {
    let idTicket = event.target.dataset.id;
    let plsCol = event.target.dataset.column;
    let plsRow = event.target.dataset.row;

    if (event.target.classList.contains('free')) {
      console.log(film)
      const myPlace = {
        'id': idTicket,
        'title': film.title,
        'row': plsRow,
        'place': plsCol,
      }

      window.M.toast({html: 'Додано в корзину'})
      event.target.classList.add('busy');
      addToLocalStorage(myPlace)
      event.target.classList.remove('free');
    } else {
      window.M.toast({html: 'Місце заняте'})
    }

  }, [addToLocalStorage, film]);

  const renderRow = useCallback((i) => {
    let rowHtml = "";
    for (let j = 1; j <= 20; j++) {
      if (Math.random() >= 0.5) {
        rowHtml += `<div class="place free" data-id=${
          i + "-" + j
        }  data-column=${j} data-row=${i}>${j}</div>`;
      } else {
        rowHtml += `<div class="place busy" data-id=${
          i + "-" + j
        } data-column=${j} data-row=${i}>${j}</div>`;
      }
    }
    return rowHtml;
  }, []);

  const renderPlaces = useCallback(() => {
    let placesHTML = "";
    for (let i = 1; i <= 14; i++) {
      placesHTML += `<div class="caption left">${i}</div>`;
      placesHTML += renderRow(i);
      placesHTML += `<div class="caption right">${i}</div>`;
    }
    setPlaces(placesHTML);
  }, [renderRow]);


  useEffect(() => {
    renderPlaces();
  }, [renderPlaces]);

  return (
    <>
      <div className="places__inner">
        <div className="box-img">
          <img src="/images/screen.png" alt="" />
          <span className="screen">Екран</span>
        </div>
        <div className="scheme-container" onClick={buyTicket}>{parse(places)}</div>

      </div>
    </>
  );
};
