import { useCallback, useEffect, useState } from "react";

export const Reviews = () => {
  const [reviews, setReviews] = useState([]);
  const [data, setData] = useState({
    user: "",
    response: "",
    date: "",
    time: "",
  });

  const changeHanlder = (event) => {
    setData({ ...data, [event.target.name]: event.target.value });
  };

  const addResponse = (event) => {
    event.preventDefault();
    const dataResponse = data;
    dataResponse.date = new Date().toLocaleDateString();
    dataResponse.time = new Date().toLocaleTimeString().slice(0, 5);
    localStorage.setItem(
      "reviews",
      JSON.stringify([...reviews, dataResponse])
    );
    setData({ user: "", response: "" });
    getReviews();
  };

  const getReviews = useCallback(() => {
    setReviews(JSON.parse(localStorage.getItem("reviews") || "[]"));
  }, []);

  useEffect(() => {
    getReviews();
  }, [getReviews]);


  return (
    <>
      <div className="row">
        <form className="form">
          <div className="row">
            <div className="input-field col s6">
              <i className="material-icons prefix">person</i>
              <input
                id="first_name"
                type="text"
                className="validate"
                maxLength="15"
                name="user"
                value={data.user}
                onChange={changeHanlder}
              />
              <label htmlFor="first_name">Ваше ім'я</label>
            </div>
          </div>
          <div className="row">
            <div className="input-field col s11">
              <i className="material-icons prefix">mode_edit</i>
              <textarea
                id="textarea1"
                className="materialize-textarea"
                maxLength="150"
                name="response"
                value={data.response}
                onChange={changeHanlder}
              ></textarea>
              <label htmlFor="textarea1">Написати відгук</label>
            </div>
          </div>
          <div className="row center">
            <button className="btn green lighten-1" onClick={addResponse}>
              Додати
            </button>
          </div>
        </form>
      </div>

      {reviews.length === 0? <div className="row"><div className="response-box center">Відгуків немає</div></div> :reviews.map((item, index) => {
        return (
          <div className="row" key={index}>
            <div className="response-box">
              <div className="row">
                <div className="col s8 user-name">{item.user}</div>
                <div className="col s4">
                  {item.date} {item.time}
                </div>
              </div>
              <div className="row">
                <p className="user-respsonse">{item.response}</p>
              </div>
            </div>
          </div>
        );
      })}
    </>
  );
};
