import React, { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Reviews } from "../components/Reviews";
import { Places } from "../components/Places";
import {films} from '../base'

const FilmDetail = () => {
  const [film, setFilm] = useState({});
  const id = useParams().id;

  const getFilm = useCallback(async () => {
    try {
      const findFilm = films.find(film => film.id === +id )
      setFilm(findFilm);
    } catch (e) {}
  }, [id]);

  useEffect(() => {
    getFilm();
  }, [getFilm]);

  useEffect(() => {
    window.M.Tabs.init(document.querySelector(".tabs"));
  });

  return (
    <>
      <h4 className="film-title">{film.title}</h4>
      <div className="row">
        <div className="film-info">
          <div className="film-baner">
            <img
              src={`${film.baner_big}`}
              alt={film.title}
            />
          </div>
          <div className="film-content">
            <dl>
              <dt>Рік</dt>
              <dd>{film.year}</dd>
              <dt>Країна</dt>
              <dd>{film.country}</dd>
              <dt>Мова</dt>
              <dd>{film.language}</dd>
              <dt>Жанр</dt>
              <dd>{film.genre}</dd>
              <dt>У головних ролях</dt>
              <dd>{film.starring}</dd>
              <dt>Режисер</dt>
              <dd>{film.director}</dd>
              <dt>Сценарій</dt>
              <dd>{film.scenario}</dd>
              <dt>Прокат</dt>
              <dd>{film.rolling}</dd>
              <dt>Знижки</dt>
              <dd>{film.discounts}</dd>
              <dt>Тривалість</dt>
              <dd>{film.duration}</dd>
              <dt>Вікові обмеження</dt>
              <dd>{film.age_restrictions}</dd>
            </dl>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col s12">
          <ul className="tabs">
            <li className="tab col s3">
              <a className="active" href="#film-description">
                Опис
              </a>
            </li>

            <li className="tab col s3">
              <a href="#film-trailer">Трейлер</a>
            </li>
            <li className="tab col s3">
              <a href="#film-reviews">Відгуки</a>
            </li>
            <li className="tab col s3">
              <a href="#places">Замовити білети</a>
            </li>
          </ul>
        </div>
        <div id="film-description" className="col s12 tabs-content">
          {film.description}
        </div>

        <div id="film-trailer" className="col s12 tabs-content">
          <div className="video-container">
            <iframe
              width="860"
              height="515"
              title={film.title}
              className="video_iframe"
              src={film.url}
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
          </div>
        </div>
        <div id="film-reviews" className="col s12 tabs-content">
          <Reviews />
        </div>
        <div id="places" className="col s12 tabs-content">
          <Places id={id} film={film}/>
        </div>
      </div>
    </>
  );
};

export default FilmDetail;
